body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  background: url(/socialApp/static/media/sky.2e047091.jpg); }

.App {
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto; }
  .App .boxes_home {
    height: 50vh;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 2rem;
    color: white;
    opacity: 0.9; }
    .App .boxes_home .box_me {
      background: white;
      border-radius: 5px;
      height: 300px;
      width: 250px;
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      margin-top: 0.5rem; }
      .App .boxes_home .box_me img {
        margin-top: 0.5rem;
        width: 200px;
        height: 200px;
        border-radius: 200px; }
      .App .boxes_home .box_me .my_email {
        margin-top: 1rem;
        color: black; }
      .App .boxes_home .box_me button {
        margin-top: 1.5rem; }
    .App .boxes_home .notesBody {
      width: 400px; }
    .App .boxes_home .other_user {
      height: 300px;
      width: 250px;
      background: white;
      border-radius: 5px; }
    .App .boxes_home .notesBody {
      background: white;
      border-radius: 5px; }
      .App .boxes_home .notesBody .noteForm {
        margin-top: 1rem; }
        .App .boxes_home .notesBody .noteForm input {
          width: 250px;
          margin-right: 0.5rem; }
      .App .boxes_home .notesBody .notex_box {
        height: 50vh;
        overflow-y: scroll; }
        .App .boxes_home .notesBody .notex_box .note {
          text-align: start; }
          .App .boxes_home .notesBody .notex_box .note p {
            margin-left: 1rem;
            display: inline-block;
            padding: 2px 7px;
            border-radius: 5px;
            background: rgba(82, 129, 199, 0.637);
            font-family: "Roboto Mono", monospace;
            color: black; }
  .App .form_block {
    background: white;
    width: 290px;
    position: relative;
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border: 5px solid white;
    border-radius: 4px;
    opacity: 0.9; }
    .App .form_block #title {
      font-family: "Roboto Mono", monospace;
      background: grey;
      padding: 0.5rem;
      color: white; }
    .App .form_block .body .error {
      font-family: "Roboto Mono", monospace; }
    .App .form_block .body form {
      display: flex;
      flex-direction: column;
      text-align: start; }
      .App .form_block .body form span {
        font-family: "Roboto Mono", monospace;
        margin: 0 0.4rem;
        font-size: 12px;
        margin-top: 1rem;
        color: rgba(128, 128, 128, 0.904); }
    .App .form_block .body .buttons {
      display: flex;
      justify-content: space-between;
      margin: 0.4rem 0.4rem; }
      .App .form_block .body .buttons .loginBtn {
        width: 70px;
        cursor: pointer;
        padding: 2px;
        font-size: 12px;
        font-family: "Roboto Mono", monospace; }
      .App .form_block .body .buttons .registerBtn {
        width: 70px;
        cursor: pointer;
        padding: 2px;
        font-size: 12px;
        font-family: "Roboto Mono", monospace; }

